import React from 'react'
import { BrowserRouter as Router, Link } from 'react-router-dom'


import Container from '../shared/template/Container'
import Footer from '../shared/template/Footer'
import Header from '../shared/template/Header'
import Topnav from '../shared/template/Topnav'
import Sidenav from '../shared/template/Sidenav'
import Toolbar from '../shared/template/Toolbar'

import Routes from '../app/Routes'

import Banner from '../shared/assets/banner-logo.png'
import Logotipo from '../shared/assets/logotipo.png'

import Espanhol from '../shared/assets/bandeiras/espanhol.png'
import Frances from '../shared/assets/bandeiras/frances.png'
import Ingles from '../shared/assets/bandeiras/ingles.png'
import Italia from '../shared/assets/bandeiras/italia.png'
import Card from '../shared/assets/rodape-cartao.png'
import Danger from '../shared/assets/danger.png'
import LogoMenuArt from '../shared/assets/menuArtLogo.png'










const App = props => {
    return (
        <Container>

            <Header>
                <img class="img-fluid" src={Banner} alt="banner-logo" />
            </Header>
            <Toolbar>
                <img class="img-fluid" src={Logotipo} alt="logotipo" />
            </Toolbar>
            <Topnav>
                <div className="tradutor">
                    <img class="img-fluid" src={Espanhol} alt="Lingua Espanhol" />
                    <img class="img-fluid" src={Frances} alt="Lingua Francês" />
                    <img class="img-fluid" src={Ingles} alt="Lingua Inglesa" />
                    <img class="img-fluid" src={Italia} alt="Lingua Japonesa" />

                </div>
            </Topnav>
            <Router>
                <Sidenav>


                    <div className="nav">


                        <div class="dropdown">
                            <button class="dropbtn pb-2">Confira nosso Cardápio</button>
                            <div class="dropdown-content">
                                <Link to="/">Pratos do Dia</Link>
                                <Link to="/lunch">Café da Manhã</Link>
                                <Link to="/cardapio">Todas as Opções</Link>

                            </div>
                        </div>

                       
                     


                        

                    </div>


                </Sidenav>

                <Routes></Routes>
            </Router>

            <Footer>
                <div className="titulos">
                    <img class="img-fluid" src={Card} alt="cartões" />
                    <div className="danger">
                        <img class="img-fluid" src={Danger} alt="proibido" />
                        <div className="lista">
                            <ul>
                                <li>É proibido a venda, oferta, fornecimento, entrega e permissão do consumo de <br/>
                                    bebida alcóolica, ainda que gratuitamente, aos menores de 18 anos de idade. <br/>
                                </li>
                                <li><strong>SE FOR DIRIGIR NÃO BEBA</strong></li>
                                <li>É proibido fumar neste estabelecimento.</li>
                            </ul>
                        </div>
                    </div>
                    <div className="rodape">
                        <img class="img-fluid" src={LogoMenuArt} alt="logo Menu Art"/>
                        <span>Menuart Cardápios | <strong>11 5588 3262</strong> | www.menuart.com.br</span>
                    </div>


                </div>


            </Footer>


        </Container>
    )
}

export default App