import React from 'react'

import styled from 'styled-components'

import ViradaPaulista from '../shared/assets/menus/virado-a-paulista.png'
import ViradaPaulistag from '../shared/assets/menus/virado-a-paulista-g.png'
import FrangoParmegiana from '../shared/assets/menus/frango-com-arroz.png'
import FrangoParmegianag from '../shared/assets/menus/frango-com-arroz-g.png'




const Dinner = props => {
    return (
        <div class="accordion" id="accordionExample">

            <div class="accordion-item">

                <h2 class="accordion-header" id="headingOne">
                    <button class="accordion-button" type="button" data-bs-toggle="collapse" data-bs-target="#collapseOne" aria-expanded="true" aria-controls="collapseOne">
                        Segunda-Feira
                    </button>
                </h2>



                <div id="collapseOne" class="accordion-collapse collapse" aria-labelledby="headingOne" data-bs-parent="#accordionExample">
                    <div class="accordion-body d-flex flex-column">


                        <div class="col-md-4">
                            <img src={ViradaPaulista} alt="Prato Salada" />
                        </div>



                        <div class="col-md-8">
                            <div class="card-body">
                                <h5 class="card-title">Virado à Paulista</h5>
                                <p class="card-text"></p>
                                <p class="card-text h4 "><small class="text-muted"><strong class="text-success">R$ 26,99</strong> </small></p>
                            </div>


                        </div>

                        <hr/>
                        <div class="col-md-4">
                            <img src={FrangoParmegiana} alt="Prato Salada" />
                        </div>

                    

                        <div class="col-md-8">
                            <div class="card-body">
                                <h5 class="card-title">Filé de Frango à Milanesa ao Creme de Milho</h5>
                                <p class="card-text"></p>
                                <p class="card-text h4 "><small class="text-muted"><strong class="text-success">R$ 26,99</strong> </small></p>
                            </div>


                        </div>


                        <button type="button" class="btn btn-primary " data-bs-toggle="modal" data-bs-target="#staticBackdrop">
                            Confira
                        </button>


                        <div class="modal fade" id="staticBackdrop" data-bs-backdrop="static" data-bs-keyboard="false" tabindex="-1" aria-labelledby="staticBackdropLabel" aria-hidden="true">
                            <div class="modal-dialog">
                                <div class="modal-content">
                                    <div class="modal-header">
                                        <h5 class="modal-title" id="staticBackdropLabel">Segunda-Feira</h5>
                                        <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>


                                    </div>



                                    <div class="modal-body d-flex flex-column" >
                                        <div class="border border-1 p-3">
                                        <h5 class="mb-3 text-primary">Virado à Paulista</h5>
                                        <img src={ViradaPaulistag} class="rounded img-fluid" alt="Prato Peixe Assado" />
                                        <h4 class="mt-3 mb-3">Ingredientes:</h4>
                                        <ul>
                                            <li>Arroz</li>
                                            <li>Tutu de feijão</li>
                                            <li>Calabresa</li>
                                            <li>Ovo e banana a milanesa</li>
                                            

                                        </ul>
                                        <p class="card-text"><small class="text-muted d-flex justify-content-end display-5"><strong class="text-success h1">R$ 26,99</strong> </small></p>
                                        </div>

                                        <div class="border border-1 p-3 mt-3">
                                        <h5 class="mt-3 mb-3 text-primary">Filé de Frango à Milanesa ao Creme de Milho</h5>
                                        <img src={FrangoParmegianag} class="rounded img-fluid" alt="Prato Peixe Assado" />
                                        <h4 class="mt-3 mb-3">Ingredientes:</h4>
                                        <ul>
                                            <li>Arroz branco</li>
                                            <li>Creme de milho</li>

                                        </ul>
                                        <p class="card-text"><small class="text-muted d-flex justify-content-end display-5 "><strong class="text-success h1">R$ 26,99</strong> </small></p>
                                        </div>
                                    </div>



                                    

                                </div>
                            </div>
                        </div>








                    </div>
                </div>
            </div>



            













            






            







        </div>
    )
}

export default Dinner; styled.div`
     width: 75vh;


`