import styled from 'styled-components'

export default styled.footer`
  border: 1px solid white;
  background-color: #694c39;
  width: 100%;
  height: 300px;

  display:flex;
    align-items: center;
    justify-content: center;

  
  .titulos {
    display:flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin: 0;
   

  }

  .titulos img {
    
    width: 40vh;
    
    
  }

  .danger img{
    width: 15vh;
    height: 10vh;
    
  }
   
   .danger {
     display: flex;
     flex-direction: row;
     width: 50vh;
     
    
   }
   .lista ul li{
     color: white;
     font-size: 12px;
     font-weight: 500;

     

   }

   .rodape img {
     width: 5vh;
     height: 6vh;
     margin-bottom: 10px;
     margin-top: 20px;
     
   }

   .rodape{
    display: flex;
     flex-direction: column;
     align-items: center;
     justify-content: center;
   }

   .rodape span{
     color: white;
     font-size: 14px;
     padding-top: 5px;
   }
`